  /* Responsive Styling */
  @media (max-width: 1200px) {
    .MuiCard-root {
      width: 70%;
    }
  }
  
  @media (max-width: 900px) {
    .MuiCard-root {
      width: 80%;
    }
  }
  
  @media (max-width: 768px) {
    .MuiCard-root {
      width: 90%;
      padding: 12px; /* Reduce padding for smaller screens */
    }
  
    .MuiCardHeader-root,
    .MuiCardContent-root {
      padding: 12px; /* Reduce padding for smaller screens */
    }
  
    .MuiCardMedia-root {
      height: auto; /* Allow image to scale proportionally */
      max-width: 100%; /* Ensure image doesn't overflow */
    }
  }
  
  @media (max-width: 600px) {
    .MuiCard-root {
      width: 95%;
      margin: 12px auto; /* Reduce margin for smaller screens */
    }
  
    .MuiTypography-h6 {
      font-size: 1.1rem; /* Adjust font size for smaller screens */
    }
  
    .MuiTypography-body2 {
      font-size: 0.9rem; /* Adjust font size for smaller screens */
    }
  }
  
  @media (max-width: 480px){
      .MuiCardHeader-avatar{
          margin-right:8px;
      }
  }