  /* Responsive Styling */
  @media (max-width: 768px) {
    .MuiTypography-h4 {
      font-size: 1.5rem;
    }
  
    .MuiTabs-root {
      margin-left: 8px;
      margin-right: 8px;
    }
  
    .MuiTab-root {
      font-size: 0.875rem;
      margin: 0 4px;
      padding: 6px 8px;
    }
  
    .MuiButton-root {
      font-size: 0.875rem;
      margin: 0 4px;
      padding: 6px 8px;
    }
  
    .MuiToolbar-root {
      padding: 8px;
    }
  
    .MuiTabs-flexContainer{
      flex-wrap: wrap;
      justify-content: center;
    }
  }
  
  @media (max-width: 600px) {
    .MuiTypography-h4 {
      font-size: 1.25rem;
    }
  
    .MuiTab-root,
    .MuiButton-root {
      font-size: 0.75rem;
      padding: 4px 6px;
    }
  
    .MuiToolbar-root {
      flex-direction: column;
      align-items: flex-start;
    }
  
    .MuiTabs-root {
      margin-top: 8px;
      margin-left: 0;
      margin-right: 0;
      width: 100%;
    }
  
    .MuiTabs-flexContainer {
      justify-content: space-around;
    }
  
    .MuiBox-root:last-child {
      margin-top: 8px;
      width:100%;
      display: flex;
      justify-content: space-around;
    }
  }
  
  @media (max-width: 400px){
      .MuiTabs-flexContainer{
          flex-direction: column;
          align-items: center;
      }
  }